$tablet: 767px;

section.add-todo {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 700px;
    height: 48px;
    display: flex;

    input {
        width: 330px;
        padding-left: 20px;
        font-size: 22px;
        margin-right: 14px;
        border-radius: 5px;

        @media (max-width: $tablet) {
            padding-left: 14px;
            margin-bottom: 20px;
            width: 90vw;
        }
    }

    button {
        height: 48px;
        font-size: 22px;
        cursor: pointer;
        padding: 0 20px;
        border-radius: 5px;
    }

    @media (max-width: $tablet) {
        width: 90vw;
        flex-wrap: wrap;
    }
}